.hero-section {
    position: relative !important;
    width: 100% !important;
    height: 80vh !important;
    overflow: hidden !important;
    margin-top: 0 !important;
    padding-top: 0 !important;
    opacity: 0 !important;
    transform: translateY(20px) !important;
    transition: all 0.8s ease-out !important;
  }
  
  .hero-section.visible {
    opacity: 1 !important;
    transform: translateY(0) !important;
  }
  
  .hero-section .carousel .slide {
    height: 80vh !important;
    position: relative !important;
  }
  
  .hero-section .carousel .slide::after {
    content: '' !important;
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    width: 100% !important;
    height: 100% !important;
    background: rgba(0, 0, 0, 0.4) !important;
    z-index: 1 !important;
  }
  
  .hero-section .carousel .slide img {
    width: 100% !important;
    height: 100% !important;
    object-fit: cover !important;
  }
  
  /* Carousel Controls */
  .hero-section .carousel .control-arrow {
    top: 50% !important;
    transform: translateY(-50%) !important;
    height: auto !important;
    padding: 20px !important;
    opacity: 0 !important;
    transition: opacity 0.3s ease !important;
    background: none !important;
  }
  
  .hero-section .carousel .control-arrow:hover {
    background: none !important;
  }
  
  .hero-section .carousel:hover .control-arrow {
    opacity: 0.8 !important;
  }
  
  .hero-section .carousel .control-arrow:before {
    border-color: #fff !important;
    border-width: 3px !important;
  }
  
  .hero-section .carousel .control-prev.control-arrow:before {
    border-right: 3px solid #fff !important;
    border-bottom: 3px solid #fff !important;
  }
  
  .hero-section .carousel .control-next.control-arrow:before {
    border-left: 3px solid #fff !important;
    border-bottom: 3px solid #fff !important;
  }
  
  .hero-section .carousel .control-dots {
    bottom: 20px !important;
    margin: 0 !important;
    padding: 0 !important;
    opacity: 1 !important;
  }
  
  .hero-section .carousel .dot {
    width: 12px !important;
    height: 12px !important;
    margin: 0 8px !important;
    background: rgba(255, 255, 255, 0.8) !important;
    box-shadow: none !important;
  }
  
  .hero-section .carousel .dot.selected {
    background: #ffffff !important;
  }
  
  .hero-section .carousel .legend {
    background: rgba(211, 211, 211, 0.0) !important;
    padding: 30px !important;
    font-size: 3rem !important;
    font-weight: 700 !important;
    color: #FAF9F6 !important;
    position: absolute !important;
    bottom: 40px !important;
    left: 0 !important;
    right: 0 !important;
    margin: 0 auto !important;
    width: 80% !important;
    max-width: 1200px !important;
    text-align: center !important;
    border-radius: 15px !important;
    box-sizing: border-box !important;
    opacity: 1 !important;
    z-index: 2 !important;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5) !important;
  }
  
  @media (max-width: 768px) {
    .hero-section .carousel .legend {
      width: 80% !important;
      font-size: 2rem !important;
      padding: 20px !important;
      right: 20px !important;
    }
  }